.footer-container {
    background-color: #242424;
    padding: 4rem 0 2rem 0;
    /* flex container expands items to fill available free space or shrinks them to prevent overflow.*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-subscription {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px;
    color: #fff;
}

.footer-subscription > p {
    font-family: 'Jura', sans-serif;
    font-weight: regular;
    line-height: 6vh;
    color: #b1b1b1;
}

.footer-subscription-heading {
    margin-bottom: 24px;
    font-size: 24px;
    font-family: 'Jura', sans-serif;
    font-weight: regular;
}

.footer-subscription-text {
    margin-bottom: 24px;
    font-size: 18px;
}

.footer-input {
    padding: 8px 20px;
    border-radius: 2px;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #fff;
}

.footer-links {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
    margin-top: 10vh;
}

.footer-link-wrapper {
    display: flex;
}

.footer-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;
}

.footer-link-items h2 {
    margin-bottom: 16px;
}

/* I'm not sure why he separated this from the above, as it doesn't seem to make a difference */
.footer-link-items > h2 {
    color: #fff;
    font-weight: bolder;
}

.footer-link-items a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 8px;
    font-family: 'Jura', sans-serif;
    font-weight: regular;
}

.footer-link-items a:hover {
    text-decoration: underline;
    color: #cfcaca;
    transition: ease-out 0.3s;
}

.footer-email-form h2 {
    margin-bottom: 32px;
}

.footer-input::placeholder {
    color: #b1b1b1;
}

/* Social Icons */
.social-icon-link {
    color: #fff;
    font-size: 24px;
}

.social-media {
    max-width: 1000px;
    width: 100%;
}

.social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin: 20px auto 0 auto;
}

.social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
}

.social-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-self: center;
    margin-bottom: 16px;
}

.website-rights {
    color: #fff;
    margin-bottom: 16px;
}

.thumbnail-logo {
    margin-top: 0;
    padding-top: 0;
    height: 80px;
    width: 80px;
    margin-bottom: 4vh;
}

@media screen and (max-width: 820px) {
    .footer-links {
        padding-top: 2rem;
    }

    .footer-input {
        width: 100%;
    }

    .btn {
        width: 100%;
    }

    .footer-link-wrapper {
        flex-direction: column;
    }

    .social-media-wrap {
        flex-direction: column;
    }
}