* {
    padding: 0px;
    margin: 0px;

    box-sizing: border-box;
}

.textbox-block {
    opacity: 0.95;
}

.textbox-container {
    width: min-content;
    height: min-content;

    overflow: hidden;

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    align-self: center;

    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20vh;

    word-wrap: break-word;

    width: 75%;
}

.textbox-vertical-block {
    width: 100%;
    max-width: 100%;
    height: 80px;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
}

.textbox-vertical-block>* {
    width: auto;
    height: 100%;

    object-fit: fill;
}

.textbox-vertical-block>*:nth-child(2) {
    flex-basis: auto;
    flex-grow: 1;
    flex-shrink: 0;
}

.textbox-center-block {
    height: auto;

    align-items: stretch;

    background-image: url('/src/images/Textbox_V2/Textbox-Center.png');
    background-size: 100% 100%;
    background-repeat: repeat;

    opacity: 0.95;
}

.textbox-center-block.textbox-vertical-block>* {
    height: initial;
}

.textbox-center-block>.textbox-block {
    width: 6px;
}

.textbox-center-block>textarea {
    width: auto;
    height: auto;

    flex-basis: auto;
    flex-grow: 1;
    flex-shrink: 1;

    margin: 16px;
    padding: 16px;
}

#text-side-border {
    width: 6px;
    height: auto;
}

.textbox-area {
    width: 10%; /* For some reason, this overlaps with the border when set to higher amount */
    height: 100%;
    text-overflow: wrap;
    padding: 2vh 4vw 2vh 4vw;
}

p {
    font-family: 'Jura', sans-serif;
    font-weight: regular;
    font-size: 3vh;
    line-height: 4.5vh;
    color: rgb(253, 241, 128);
    text-shadow: 1px 1px #534b17;
}

@media only screen and (max-width: 1024px) {
    .textbox-container {
        width: 94%;
    }
  }